import "./styles.css";

// initial todos
// DO NOT EDIT THIS ARRAY
// You may add props to objects if needed.
let todos = [
  {
    todoID: 0,
    todoText: "Finish Homework",
    todoComplete: false
  },
  {
    todoID: 1,
    todoText: "Walk the dog",
    todoComplete: true
  },
  {
    todoID: 2,
    todoText: "Clean my room",
    todoComplete: false
  }
];

let taskList = document.querySelector("#list");

function showToDos() {
  taskList.innerHTML = "";

  for (let i in todos) {
    let item = document.createElement("li");
    let trashBtn = '<span> <i class="fa fa-trash"></i></span>';
    let editBtn = '<span class="editBtn"><i class="fa fa-edit"></i></span>';

    if (todos[i].todoComplete) {
      item.classList.add("done");
      item.innerHTML = todos[i].todoText + trashBtn;
    } else {
      item.innerHTML = todos[i].todoText + editBtn + trashBtn;
    }

    item.id = `task${todos[i].todoID}`;

    taskList.appendChild(item);
  }
}

showToDos();

let addBtn = document.querySelector("#addTask");
let taskText = document.querySelector("#taskInput");

function addToDos() {
  let newTask = {
    todoID: todos.length,
    todoText: taskText.value,
    todoComplete: false
  };

  todos.push(newTask);
  showToDos();
}

taskText.addEventListener("keyup", (event) => {
  if (event.code === "Enter") {
    addToDos();
  }
});
addBtn.addEventListener("click", (event) => {
  addToDos();
});

let doneBtn = document.querySelector("#clear");
doneBtn.addEventListener("click", (event) => {
  for (let i in todos) {
    if (todos[i].todoComplete === false) {
      todos.splice(todos[i], 1);
    }
  }

  showToDos();
});
